.image-preload {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-size: cover;
  -webkit-filter: blur(5px);
          filter: blur(5px);
}

.image-loaded {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-size: cover;
  opacity: 0;
  transition: opacity 1s ease;
}

.image-fade-in {
  opacity: 1;
}

.image-hide {
  opacity: 0;
}

