/* animated checkmark */
.circle {
  stroke-dasharray: 166;
  stroke-dashoffset: 166;
  stroke-width: 2;
  stroke-miterlimit: 10;
  stroke: #f40ef0;
  fill: none;
  -webkit-animation: stroke 0.6s cubic-bezier(0.65, 0, 0.45, 1) forwards;
          animation: stroke 0.6s cubic-bezier(0.65, 0, 0.45, 1) forwards;
}

.checkmark {
  width: 2em;
  height: 2em;
  border-radius: 50%;
  display: inline-block;
  stroke-width: 2;
  stroke: #fff;
  stroke-miterlimit: 10;
  margin: 0;
  box-shadow: inset 0px 0px 0px #f40ef0;
  -webkit-animation: fill 0.4s ease-in-out 0.4s forwards,
    scale 0.3s ease-in-out 0.9s both;
          animation: fill 0.4s ease-in-out 0.4s forwards,
    scale 0.3s ease-in-out 0.9s both;
}

.check {
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  stroke-dasharray: 48;
  stroke-dashoffset: 48;
  -webkit-animation: stroke 0.3s cubic-bezier(0.65, 0, 0.45, 1) 0.8s forwards;
          animation: stroke 0.3s cubic-bezier(0.65, 0, 0.45, 1) 0.8s forwards;
}

@-webkit-keyframes stroke {
  100% {
    stroke-dashoffset: 0;
  }
}

@keyframes stroke {
  100% {
    stroke-dashoffset: 0;
  }
}

@-webkit-keyframes scale {
  0%,
  100% {
    -webkit-transform: none;
            transform: none;
  }
  50% {
    -webkit-transform: scale3d(1.1, 1.1, 1);
            transform: scale3d(1.1, 1.1, 1);
  }
}

@keyframes scale {
  0%,
  100% {
    -webkit-transform: none;
            transform: none;
  }
  50% {
    -webkit-transform: scale3d(1.1, 1.1, 1);
            transform: scale3d(1.1, 1.1, 1);
  }
}

@-webkit-keyframes fill {
  100% {
    box-shadow: inset 0px 0px 0px 30px #f40ef0;
  }
}

@keyframes fill {
  100% {
    box-shadow: inset 0px 0px 0px 30px #f40ef0;
  }
}

/* bird */

#bird {
  -webkit-animation: fly 0.8s ease-in-out infinite;
          animation: fly 0.8s ease-in-out infinite;
}

#rightwing {
  -webkit-animation: rightwing 0.25s ease-in-out infinite;
          animation: rightwing 0.25s ease-in-out infinite;
  -webkit-transform-origin: 330px 350px;
          transform-origin: 330px 350px;
}

#leftwing {
  -webkit-animation: leftwing 0.25s ease-in-out infinite;
          animation: leftwing 0.25s ease-in-out infinite;
  -webkit-transform-origin: 90px 390px;
          transform-origin: 90px 390px;
}

#leftpupil {
  -webkit-animation: leftpupil 12s ease-in-out infinite;
          animation: leftpupil 12s ease-in-out infinite;
}

#rightpupil {
  -webkit-animation: rightpupil 12s ease-in-out infinite;
          animation: rightpupil 12s ease-in-out infinite;
}

@-webkit-keyframes fly {
  from {
    -webkit-transform: translateY(0px);
